.header-contacts-block {
  display: flex;
  align-items: center;
  gap: 30px;

  @media (max-width: 1366px) {
    order: -1;
  }

  &__left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0;

    @media (max-width: 1366px) {
      display: none;
    }
  }

  &__tel {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #16110D;
  }

  &__work {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
  }

  &__email {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #2980B9;
  }

  &__socials {
    gap: 10px;
    align-items: center;
    display: flex;

    img {
      height: 40px;
      width: auto;
      display: block;
    }
  }
}