.article-author {
  display: grid;
  grid-template-columns: 213px 1fr;
  align-items: flex-start;
  gap: 30px;

  @media (max-width: 655px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }

  &__left {
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;

    &>img {
      display: block;
      border-radius: 100%;
      width: 80px;
      height: 80px;
      object-fit: cover;
      object-position: center;
      border: 2px solid #FFBE2E;
    }
  }

  &__author {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    text-align: center;
    font-weight: 500;
    font-size: 13px;
    line-height: 18px;
    color: #536D79;

    a {
      color: #6699CC;
      text-decoration: underline dotted;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;

    @media (max-width: 655px) {
      text-align: center;
      align-items: center;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    color: #253137;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #536D79;
  }
}