.article-links {
  
  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #16110D;
    margin-bottom: 15px;
  }

  ul {
    padding-left: 0;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 15px;
  }

  li {
    &::before, &::after {
      content: none;
    }

    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #2980B9;
    margin-bottom: 0;

    display: flex;
    align-items: center;
    gap: 20px;

    &>svg {
      height: 21px;
      width: auto;
      flex-shrink: 0;
    }
  }
}