.quote-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px;
  background: #FFEECC;
  border-radius: 30px;
  gap: 10px;
  margin: 0;

  @media (max-width: 655px) {
    padding: 20px;
  }

  blockquote {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #253137;
    margin: 0;

    @media (max-width: 655px) {
      font-size: 18px;
    }
  }

  figcaption {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
  }
}