.article-top-info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 15px 40px;
  padding-inline-start: 0;
  padding-top: 50px;

  @media (max-width: 655px) {
    gap: 15px;
    padding-top: 30px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    list-style: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
    margin-bottom: 0;

    &::before, &::after {
      content: none;
    }

    svg {
      height: 24px;
      width: auto;
      vertical-align: middle;
    }

    @media (max-width: 655px) {
      flex-direction: row;
      align-items: center;

      &:first-child {
        width: 100%;
      }

      span:first-child:not(.--no-mobile-hide) {
        display: none;
      }
    }
  }
}