.blog-banner {
  position: relative;
  background: #FFBE2E;
  overflow: hidden;
  min-height: 290px;
  padding: 20px 0;

  @media (max-width: 1366px) {
    
  }

  @media (max-width: 655px) {
    min-height: 270px;
  }

  .radial-bottom-shadow {
    background: #FFFFFF;
  }

  .content-inner {
    position: relative;
    z-index: 100;
    display: flex;
    flex-direction: column;
  }

  #breadcrumbs {
    position: static;
    z-index: 100;
    top: 15px;

    span:last-child {
      color: #16110D;
    }
  }

  &__title {
    margin-top: auto;
    margin-bottom: auto;
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    text-align: center;
    text-transform: uppercase;
    color: #16110D;
    margin-top: 60px;

    @media (max-width: 1366px) {
      margin-top: 40px;
    }

    @media (max-width: 665px) {
      font-size: 30px;
      line-height: 50px;
      margin-top: 10px;
    }
  }
}