.expert-banner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px 40px;
  background: linear-gradient(107.06deg, #FFEECC 1.09%, #FFBE2E 100%);
  border-radius: 20px;

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    color: #16110D;
    text-align: left;

    @media (max-width: 655px) {
      text-align: center;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #16110D;

    @media (max-width: 655px) {
      text-align: center;
    }
  }

  &__button {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px 50px;
    background: #16110D;
    box-shadow: 2px 2px 0px #FFEECC;
    border-radius: 50px;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #FDFFFF;
    transition: .3s ease;

    @media (max-width: 655px) {
      width: 100%;
    }

    @media (any-hover: hover) {
      &:hover {
        background: #FDFFFF;
        color: #16110D;
        box-shadow: 2px 2px 0px #16110D;
      }
    }
  }
}