.themes-nav {
  margin-top: 50px;
  margin-bottom: 80px;

  @media (max-width: 665px) {
    margin-top: 40px;
    margin-bottom: 30px;
  }

  &__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    color: #16110D;
    margin-bottom: 30px;
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    max-width: 1235px;
    padding-bottom: 10px;
    
    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }
    &::-webkit-scrollbar-thumb {
      background: #E1E1E3;
      border-radius: 5px;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }

    @media (max-width: 665px) {
      flex-wrap: nowrap;
      overflow-x: auto;
    }

    @media (max-width: 460px) {
      padding-right: 20px;
      margin-right: -20px;
    }

    button {
      font-weight: 600;
      font-size: 12px;
      line-height: 20px;
      color: #7F8C8D;
      padding: 4px 12px;
      box-sizing: border-box;
      display: block;
      padding: 4px 12px;
      border: 1px solid #7F8C8D;
      border-radius: 50px;
      background: #FFFFFF;
      transition: .3s ease;
      flex-shrink: 0;

      @media (any-hover: hover) {
        &:hover {
          border-color: #16110D;
        }
      }

      &.--active {
        color: #16110D;
        border-color: #16110D;
        background: #FFEECC;
      }
    }
  }
}