.article-anchors {
  gap: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &__title {
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: #253137;
    margin: 0;
  }

  ul {
    padding-left: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin: 0;

    li {
      &::before, &::after { content: none; }
      margin-bottom: 0;

      a {
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        text-decoration: underline dotted;
        color: #2980B9;
      }
    }
  }
}