.header-contacts {
  display: flex;
  align-items: center;
  gap: 30px;

  @media (max-width: 1366px) {
    order: -1;
    right: 0;
  }

  @media (max-width: 665px) {
    width: auto !important;
    right: 20px;

    .button {
      display: none;
    }
  }
  
  &.cols {

    @media (min-width: 481px) {
      float: none;
      margin: 0;
    }
  }
}

.header .logo {

  @media (max-width: 665px  ) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  @media (max-width: 665px) {
    gap: 5px;
    
    a {
      font-size: 22px;
    }
  }
}

.main-nav {
  column-gap: 10px;

  li {
    position: relative;
  }

  @media (max-width: 1366px) {
    justify-content: flex-start;
    padding-top: 30px;
  }
}

.submenu {
  z-index: 100;
  text-align: left;
  padding: 15px;
  width: 227px;

  li {
    margin-bottom: 10px;
    margin-right: 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  li a {
    text-align: left;
  }

  .content-inner {
    padding: 0;
  }

  .main-nav {
    padding: 0;
  }
}

.header {

  @media (max-width: 1366px) {
    padding: 20px 0;
  }
}

.icon-spinner {
  mask-image: url('data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20height%3D%2224px%22%20viewBox%3D%220%20-960%20960%20960%22%20width%3D%2224px%22%20fill%3D%22currentColor%22%3E%3Cpath%20d%3D%22M480-80q-82%200-155-31.5t-127.5-86Q143-252%20111.5-325T80-480q0-83%2031.5-155.5t86-127Q252-817%20325-848.5T480-880q17%200%2028.5%2011.5T520-840q0%2017-11.5%2028.5T480-800q-133%200-226.5%2093.5T160-480q0%20133%2093.5%20226.5T480-160q133%200%20226.5-93.5T800-480q0-17%2011.5-28.5T840-520q17%200%2028.5%2011.5T880-480q0%2082-31.5%20155t-86%20127.5q-54.5%2054.5-127%2086T480-80Z%22%2F%3E%3C%2Fsvg%3E');
  animation: spin 1s linear infinite;
  display: inline-block;
  background: #000000;
  width: 30px;
  height: 30px;
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: contain;
}

.content-inner-article {
  @media (min-width: 1367px) {
    max-width: 946px;
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg)
  }
}

