.blog-card {

  &__inner {
    display: flex;
    flex-direction: column;
  }

  img {
    width: 100%;
    max-width: 100%;
    height: 190px;
    display: block;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    box-shadow: 4px 4px #FFBE2E;
    margin-bottom: 20px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 10px;
  }

  &__tag {
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    color: #7F8C8D;
    padding: 4px 12px;
    box-sizing: border-box;
    display: block;
    padding: 4px 12px;
    border: 1px solid #7F8C8D;
    border-radius: 50px;
    background: #FFFFFF;
    transition: .3s ease;
    flex-shrink: 0;
  }

  &__date {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
  }

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #16110D;
    margin-bottom: 20px;
    transition: .3s ease;
    text-transform: none;

    @media (max-width: 665px) {
      text-align: left;
    }
  }

  &__bottom {
    margin-top: auto;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
    display: flex;
    align-items: center;
    gap: 2px 15px;
    flex-wrap: wrap;

    span svg {
      margin-right: 5px;
      height: 24px;
      width: auto;
      vertical-align: middle;
    }
  }

  @media (any-hover: hover) {
    &:hover &__title {
      color: #FFBE2E;
    }
  }
}