.video {
  position: relative;

  video {
    position: relative;
  }

  &__button {
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    width: 60px;
    height: 60px;
    background: #FFBE2E;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
    border-radius: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &.--hidden {
      display: none;
    }

    svg {
      
    }
  }
}