.article-photo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;

  .video, video, img, figcaption {
    width: 100%;
    max-width: 702px;

    @media (max-width: 1366px) {
      max-width: 454px;
    }
    
  }

  img, video {
    border-radius: 20px;
    max-height: 460px;
    object-fit: cover;
    object-position: center;

    @media (max-width: 1366px) {
      max-height: 297px;
    }

    @media (max-width: 655) {
      max-height: 219px;
    }
  }

  figcaption {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: -0.02em;
    color: #4A4B57;
  }
}