.article-bottom {
  display: flex;
  flex-direction: column;
  padding: 25px 0px;
  gap: 50px;
  border-top: 1px solid #CCCCCC;
  border-bottom: 1px solid #CCCCCC;

  &__bottom {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 20px;

    @media (max-width: 655px) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__coll {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  &__label {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #253137;
  }

  &__like {
    border: 1px solid #7F8C8D;
    border-radius: 50px;
    padding: 8px 15px;
    font-weight: 500;
    font-size: 13px;
    line-height: 18px;
    color: #16110D;
    gap: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: .3s ease;

    svg {
      height: 16px;
      width: auto;
    }

    @media (any-hover: hover) {
      &:hover {
        color: #FFBE2E;
        border-color: #FFBE2E;
      }
    }
  }

  &__share {
    display: flex;
    align-items: center;
    gap: 15px;

    a {
      display: inline-block;
      color: #7F8C8D;
      transition: .3s ease;

      svg {
        height: 24px;
        width: auto;
      }

      @media (any-hover: hover) {
        &:hover {
          color: #253137;
        }
      }
    }
  }
}