.blog-articles {
  margin-top: 90px;
  position: relative;
  margin-bottom: -50px;
  padding-bottom: 140px;
  overflow: hidden;

  @media (max-width: 1366px) {
    margin-bottom: -30px;
  }

  @media (max-width: 665px) {
    margin-top: 40px;
    padding-bottom: 80px;
    margin-bottom: -20px;
  }

  .radial-bottom {
    background: #FFFFFF;
  }

  .content-inner {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 100;
  }

  &__title {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 30px;
    line-height: 50px;
    color: #16110D;
  }

  .button {
    background: #FFFFFF;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #16110D;
    margin-top: 90px;
    position: relative;

    @media (max-width: 665px) {
      margin-top: 40px;
    }

    .icon-spinner {
      position: absolute;
      width: 40px;
      height: 40px;
      background: #16110D;
      top: 50%;
      left: 50%;
      translate: -50% -50%;
      display: none;
    }

    &.--is-loading {
      span {
        opacity: 0;
      }

      .icon-spinner {
        display: inline-block;
      }
    }
  }
}