.table {
  overflow-x: auto;
  padding-bottom: 10px;
    
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  &::-webkit-scrollbar-thumb {
    background: #E1E1E3;
    border-radius: 5px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }

  table {
    border-spacing: 0;
  }

  th, td {
    border-right: 1px solid #FFFFFF;

    &:last-child {
      border-right: none;
    }
  }

  thead th {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #4A4B57;
    text-align: left;
    vertical-align: bottom;
    padding: 15px;
    background: #F0F0F1;
    
    min-width: 171px;

    @media (max-width: 655px) {
      min-width: 275px;
    }
  }

  tbody td {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #19191D;
    border-bottom: 1px solid #D2D2D6;
    padding: 15px;
    vertical-align: top;

    b {
      font-weight: 700;
    }

    em {
      font-style: italic;
    }
  }
}