.article-numlist-item {
  display: flex;
  align-items: center;
  gap: 20px;

  @media (max-width: 665px) {
    flex-direction: column;
    align-items: flex-start;
  }

  &>i {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    max-width: 32px;
    max-height: 32px;
    background: #FFFFFF;
    border: 2px solid #FFBE2E;
    border-radius: 50px;
    text-align: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #16110D;
    font-style: normal;
    padding: 0 0 0 2px;
    flex-shrink: 0;
  }

  &>p {
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    color: #253137;
    margin-bottom: 0;
  }
}