.blog-card-big {
  box-sizing: border-box;
  padding: 30px 50px 30px 30px;
  background: #FDFFFF;
  border: 1px solid #ECF0F1;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  display: block;

  @media (max-width: 1366px) {
    padding: 20px;
  }

  &__inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;

    @media (max-width: 1366px) {
      display: flex;
      flex-direction: column;
    }
  }

  &__image {
    position: relative;
    height: 374px;

    @media (max-width: 1366px) {
      display: flex;
      flex-direction: column;
      height: 344px;
    }

    @media (max-width: 665px) {
      height: 156px;
    }
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
  }

  &__right {
    display: flex;
    flex-direction: column;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 15px;
  }

  &__tag {
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    color: #7F8C8D;
    padding: 4px 12px;
    box-sizing: border-box;
    display: block;
    padding: 4px 12px;
    border: 1px solid #7F8C8D;
    border-radius: 50px;
    background: #FFFFFF;
    transition: .3s ease;
    flex-shrink: 0;
  }

  &__date {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
  }

  &__title {
    font-weight: 700;
    font-size: 30px;
    line-height: 50px;
    color: #16110D;
    margin-bottom: 15px;
    transition: .3s ease;
    text-transform: none;

    @media (max-width: 665px) {
      text-align: left;
    }
  }

  &__bottom {
    margin-top: auto;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7F8C8D;
    display: flex;
    align-items: center;
    gap: 2px 30px;
    flex-wrap: wrap;

    span svg {
      margin-right: 5px;
      height: 24px;
      width: auto;
      vertical-align: middle;
    }
  }

  @media (any-hover: hover) {
    &:hover &__title {
      color: #FFBE2E;
    }
  }
}