.article-content {
  padding-top: 50px;
  padding-bottom: 100px;

  @media (max-width: value) {
    padding-top: 40px;
    padding-bottom: 90px;
  }

  .article-intro:not(:last-child) {
    margin-bottom: 90px;

    @media (max-width: 1366px) {
      margin-bottom: 40px;
    }
  }

  .article-photo:not(:last-child) {
    margin-bottom: 90px;

    @media (max-width: 1366px) {
      margin-bottom: 40px;
    }
  }

  &>h2 {
    font-weight: 700;
    font-size: 30px;
    line-height: 50px;
    color: #253137;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &>p {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #16110D;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &>ul {
    gap: 25px;
    display: flex;
    flex-direction: column;
    gap: 25px;

    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  &>ul li {
    position: relative;
    margin-bottom: 0;

    &::before, &::after {
      top: 50%;
      translate: 0 calc(-50% - 2px);
    }

    &::before {
      margin-top: 0px;
    }
  }

  .quote-block:not(:last-child) {
    margin-bottom: 50px;
  }

  .article-links:not(:last-child) {
    margin-bottom: 90px;

    @media (max-width: 1366px) {
      margin-bottom: 40px;
    }
  }

  .article-numlist-item:not(:last-child) {
    margin-bottom: 25px;
  }

  .article-alert:not(:last-child) {
    margin-bottom: 25px;
  }

  .table:not(:last-child) {
    margin-bottom: 90px;

    @media (max-width: 1366px) {
      margin-bottom: 40px;
    }
  }

  .expert-banner:not(:last-child) {
    margin-bottom: 90px;

    @media (max-width: 1366px) {
      margin-bottom: 40px;
    }
  }
}