.article-intro {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px;
  gap: 25px;
  background: #FFFFFF;
  border: 1px solid #ECF0F1;
  border-radius: 10px;

  @media (max-width: 655px) {
    padding: 20px;
  }

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #253137;
    margin: 0;
  }

  &>p {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #16110D;
  }
}